'use client';

import { useEffect, useState } from 'react';
import GeneratedQuestion from './components/GeneratedQuestion';
import QuestionList from './components/QuestionList';
import Header from './components/Header';
import Footer from './components/Footer';
import { fetchData } from './services/api';

interface BackendData {
  data: string;
}

export default function Home() {
  const [data, setData] = useState<BackendData | null>(null);
  const [questions, setQuestions] = useState<string[]>([]);
  const [generatedQuestion, setGeneratedQuestion] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const fetchGeneratedQuestion = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await fetchData('/api/generate-question');
      setGeneratedQuestion(response.question);
    } catch (error) {
      setError('Failed to fetch the generated question.');
      console.error('Error fetching generated question:', error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    const fetchInitialData = async () => {
      try {
        const backendData = await fetchData("/api/data");
        setData(backendData);

        const questionData = await fetchData('/api/questions');
        setQuestions(questionData.questions);

        await fetchGeneratedQuestion();
      } catch (error) {
        setError('Failed to load initial data.');
        console.error('Error loading initial data:', error);
      }
    };

    fetchInitialData();
  }, []);

  return (
    <div className="flex flex-col min-h-screen">
      <Header />
      <main className="flex-grow p-4">
        <h1 className="text-2xl font-bold mb-4">Welcome to the Real Estate Exam AI!</h1>

        {error && (
          <div className="bg-red-100 text-red-700 p-4 rounded-lg mb-4">
            <p>{error}</p>
          </div>
        )}

        {data ? (
          <p className="mb-4">Data from backend: {data.data}</p>
        ) : (
          <p className="mb-4">Loading backend data...</p>
        )}

        <QuestionList questions={questions} />

        <GeneratedQuestion
          question={generatedQuestion}
          onGenerate={fetchGeneratedQuestion}
          loading={loading}
        />
      </main>
      <Footer />
    </div>
  );
}